/* Services Section */
#services {
    padding: 100px 0;
    background: linear-gradient(to right, #3c80c1 0%, #81c3d7 100%);
    color: #fff;
}
#services .service-desc {
    margin: 10px 10px 20px;
}
#services h2 {
    color: #fff;
}
#services .section-title h2::after {
    position: absolute;
    content: '';
    background: rgba(255, 255, 255, 0.3);
    height: 4px;
    width: 60px;
    bottom: 0;
    margin-left: -30px;
    left: 50%;
}
.services-img {
    width: 240px;
    height: 240px;
    padding: 20px 5px;
    border-radius: 50%;
}
#services h3 {
    font-weight: 500;
    padding: 5px 0;
    color: #fff;
}
#services p {
    color: rgba(255, 255, 255, 0.75);
}
#services .service-desc {
    margin-bottom: 40px;
}
